import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi ,CityApi,Equipments_masterApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-specialization.component.html'
})
export class AddSpecializationComponent {
	@ViewChild('equipmentName') name: NgModel;
	display: boolean = false; result: any;
	submitted: boolean = false;
	equipmentName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	data: any;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	equipmentStatus:boolean = false;

	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private cityApi:CityApi,private equipmentApi:Equipments_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		
		this.rolename = this.userDetails.userDet.roles;
	}


	async submit(form) {
		this.submitted = true;
		let validFlag = true;
		if (form.valid) {

			//function call to change base64 format data to Image
			var dataArrObj = {
				"equipment_name": this.capital_letter(this.equipmentName),
				"status": (this.equipmentStatus)?true:false
			}

			this.loading = true;
			this.ifNameExist(dataArrObj).then((total)=>{
				if(total > 0){
					this.loading = false;
					this.result = "Equipment name already exist";
					this.display = true;
				}else{
					this.equipmentApi.upsert(dataArrObj).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{

				this.equipmentApi.getEquipmentCountName({ "equipment_name": dataObj.equipment_name}).subscribe((data)=>{
					resolve(data.result[0].total);
		
					},err=>{
						reject(err);
				});
			
			}catch(err){
				reject(err);
			}
		});
		
		
	}

	ngOnInit(): void {

	}

	capital_letter(str) 
	{
		str = str.split(" ");

		for (var i = 0, x = str.length; i < x; i++) {
			str[i] = str[i][0].toUpperCase() + str[i].substr(1);
		}

		return str.join(" ");
	}


	goToListing() {
		// window.location.reload();
		this.router.navigate(["equipment"]);
	}

	
}


