import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AddSpecializationComponent } from './add-specialization.component';
import { SpecializationCrudRoutingModule } from './add-specialization-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    SpecializationCrudRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    AddSpecializationComponent
  ]
})
export class AddSpecializationModule { }



